import {IHeroPack} from "../types/IHeroPack";
import React from "react";
import {Box, Chip, Container, Divider, ToggleButton, ToggleButtonGroup} from "@mui/material";
import {HeroCounterHeroGrid, HeroCounterPackGrid} from "./index";
import {HeroData} from "../types";
import HeroRouletteDefendersMode from "./heroRouletteDefendersMode";
import DeleteIcon from "@mui/icons-material/Delete";
import {MySettings} from "../types/MySettings";

interface IProps {
    currentMode: number,
    heroFilterInclude: number[]
    heroFilterExclude: number[]
}

interface IState {
    currentMode: number,
    heroFilterInclude: number[],
    heroFilterExclude: number[],
    requestsInProgress: number,
    heroIds: IHeroPack[],
    heroProbs: Map<number,number>
}

export class HeroBestPackMode extends React.Component<IProps, IState> {
    constructor(props:any) {
        super(props);
        const heroProbs=new Map<number,number>()
        this.state={
            currentMode: props.currentMode,
            heroFilterInclude: props.heroFilterInclude,
            heroFilterExclude: props.heroFilterExclude,
            requestsInProgress: 0,
            heroIds: [],
            heroProbs: heroProbs
        }
        this.setMode=this.setMode.bind(this)
        this.onHeroGridClick=this.onHeroGridClick.bind(this)
        this.handleDeleteFilterChip=this.handleDeleteFilterChip.bind(this)
        this.renderFilterBar=this.renderFilterBar.bind(this)
        for (const x of HeroData.allHeroesIds) {
            this.state.heroProbs.set(x,-1)
        }
        this.updateHeroes(props.heroFilterInclude,props.heroFilterExclude,props.currentMode,heroProbs)
    }
    requestSeq: number =0
    requestsInProgress: number =0

    updateHeroes(heroFilterInclude:number[],heroFilterExclude:number[],currentMode:number,heroProbs:Map<number,number>) {
        const requestId=this.requestSeq++

        this.requestsInProgress++
        this.setState({requestsInProgress:this.requestsInProgress})

        let gMode='a'
        switch (currentMode) {
            case 0: {gMode='a';break;}
            case 1: {gMode='d';break;}
            case 8010: {gMode='g8010';break;}
            case 8011: {gMode='g8011';break;}
            case 8012: {gMode='g8012';break;}
            case 8013: {gMode='g8013';break;}
            case 8014: {gMode='g8014';break;}
            case 8015: {gMode='g8015';break;}
        }
        fetch(MySettings.apiUrl+"topPacks2/"+gMode+"/"+HeroData.constructHeroFilterNew(heroFilterInclude)+"/10000%3A"+heroFilterExclude.join(":") , {
            method: "GET"
        }).then(res => res.json())
            .then(
                (result) => {
                    if (result['items']) {
                        const heroLostPacks = []
                        let cnt = 0
                        for (const x of Array.from(heroProbs.keys())) {
                            heroProbs.set(x,-1)
                        }
                        const heroUniqMap=new Map<string,number>()
                        for (const x in result['items']) {
                            const z = result['items'][x]
                            let lostPack = [~~z.d1, ~~z.d2, ~~z.d3, ~~z.d4, ~~z.d5]
                            //console.log('LPU '+lostPack.join(',')+" wr="+(z['avgwinrate']*100).toFixed(1)+" lr="+(z['avglossrate']*100).toFixed(1))

                            for (const heroId of lostPack) {
                                let m = heroProbs.get(~~heroId)
                                if (m && m>=0) {
                                    m++
                                } else {
                                    m = 1
                                }
                                heroProbs.set(~~heroId, m)
                            }
                            cnt++
                            let extText=""
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"sumwinrate","    ",100,"%",1)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"avgwinrate","    ",100,"%",1)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"cntwinpacks","     ",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"winsa","    ",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"cntfightsa","    ",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"sumlossrate","    ",100,"%",1)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"avglossrate","    ",100,"%",1)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"cntlosspacks","    ",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"winsd","    ",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"cntfightsd","    ",1,"",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h0d","  ",1/1000,"k",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h1d","  ",1/1000,"k",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h2d","  ",1/1000,"k",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h3d","  ",1/1000,"k",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h4d","  ",1/1000,"k",0)
                            extText+=HeroRouletteDefendersMode.formatParameter(z,"h5d","  ",1/1000,"k",0)

                            if (z.shd && z.bp) {
                                const packs=z.bp.split(":")
                                const pack1=packs[1]?.split("=")
                                const pack2=packs[2]?.split("=")
                                if (pack1) {
                                    extText+="    :\r\n"
                                    extText+="1) "+HeroData.translateHeroIdsArray(lostPack)+"  "+(~~z.o2/1000)?.toFixed(1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")+"k\r\n"
                                    extText+="2) "+(pack1[0]?HeroData.translateHeroIdsArray(pack1[0]?.split(","))+"  "+(~~pack1[1]/1000)?.toFixed(1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")+"k":"???")+"\r\n"
                                    extText+="3) "+(pack2 && pack2[0]?HeroData.translateHeroIdsArray(pack2[0]?.split(","))+"  "+(~~pack2[1]/1000)?.toFixed(1).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")+"k":"???")+"\r\n"
                                    extText+="   : "+((~~z.shd/1000)?.toFixed(1)).toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")+"k\r\n"
                                }
                            }
                            //lostPack.push(Number.parseFloat(z.r))
                            //console.log('XXX5',z)
                            if (!heroUniqMap.get(lostPack.join(','))) {
                                heroLostPacks.push({hIds:lostPack,extendedInfo:{statusInfo:extText.trim(),wra:z.avgwinrate*100,wrd:z.avglossrate*100,ca:z.cntfightsa,cd:z.cntfightsd}})
                                heroUniqMap.set(lostPack.join(','),1)
                            }

                            //console.log('HLP',heroLostPacks)
                        }
                        for (const heroPackId of Array.from(heroProbs.keys())) {
                            const mmd = heroProbs.get(~~heroPackId)
                            if (mmd && cnt > 0) heroProbs.set(~~heroPackId, 100 * mmd / cnt)
                        }
                            this.setState(
                                {heroIds: heroLostPacks, heroProbs: heroProbs}
                            )

                    }
                }).finally(()=>{
            this.requestsInProgress--
            this.setState({requestsInProgress:this.requestsInProgress})
        })
    }

    onHeroGridClick(heroId: number) {
        //console.log('onHeroGridClick', heroId, heroFilterInclude, heroFilterExclude)
        const newHeroFilterInclude = [...this.state.heroFilterInclude]
        const newHeroFilterExclude = [...this.state.heroFilterExclude]
        const includeIndex = newHeroFilterInclude.indexOf(heroId)
        const excludeIndex = newHeroFilterExclude.indexOf(heroId)
        if (includeIndex >= 0) {
            newHeroFilterInclude.splice(includeIndex, 1)
            this.setState({heroFilterInclude:newHeroFilterInclude})
            if (excludeIndex < 0) {
                newHeroFilterExclude.push(heroId)
                this.setState({heroFilterExclude:newHeroFilterExclude})
            }
            this.updateHeroes(newHeroFilterInclude,newHeroFilterExclude, this.state.currentMode, this.state.heroProbs)
        } else if (excludeIndex >= 0) {
            newHeroFilterExclude.splice(excludeIndex, 1)
            this.setState({heroFilterExclude:newHeroFilterExclude})
            this.updateHeroes(newHeroFilterInclude,newHeroFilterExclude, this.state.currentMode, this.state.heroProbs)
        } else {
            if (newHeroFilterInclude.length < 5) {
                newHeroFilterInclude.push(heroId)
                this.setState({heroFilterInclude:newHeroFilterInclude})
                this.updateHeroes(newHeroFilterInclude,newHeroFilterExclude, this.state.currentMode, this.state.heroProbs)
            }
        }
    }
    
    /*filterHero(heroId: number) {
        if (HeroData.allHeroesIds.includes(heroId)) {
            const heroesFilter = this.state.heroFilterInclude
            const index = heroesFilter.indexOf(heroId, 0)
            if (index > -1) {
                heroesFilter.splice(index, 1);
                this.setState({heroFilterInclude: heroesFilter})
                this.updateHeroes(heroesFilter, this.state.currentMode, this.state.heroProbs)
            } else {
                if (heroesFilter.length < 5) {
                    heroesFilter.push(heroId)
                    this.setState({heroFilterInclude: heroesFilter})
                    this.updateHeroes(heroesFilter, this.state.currentMode, this.state.heroProbs)
                }
            }
        }
    }*/

    handleDeleteFilterChip(e: any) {
        let filterType = undefined
        let heroId = undefined
        if (e?.target?.parentNode?.parentNode?.dataset?.filtertype) {
            filterType = e?.target?.parentNode?.parentNode?.dataset?.filtertype
            heroId = ~~e?.target?.parentNode?.parentNode?.dataset?.filterheroid
        } else if (e?.target?.parentNode?.dataset?.filtertype) {
            filterType = e?.target?.parentNode?.dataset?.filtertype
            heroId = ~~e?.target?.parentNode?.dataset?.filterheroid
        }
        if (filterType && heroId) {
            switch (filterType) {
                case "include": {
                    const index = this.state.heroFilterInclude.indexOf(heroId)
                    if (index >= 0) {
                        const newHeroFilterInclude = [...this.state.heroFilterInclude]
                        newHeroFilterInclude.splice(index, 1)
                        this.setState({heroFilterInclude: newHeroFilterInclude})
                        this.updateHeroes(newHeroFilterInclude,this.state.heroFilterExclude, this.state.currentMode, this.state.heroProbs)
                    }
                    break;
                }
                case "exclude": {
                    const index = this.state.heroFilterExclude.indexOf(heroId)
                    if (index >= 0) {
                        const newHeroFilterExclude = [...this.state.heroFilterExclude]
                        newHeroFilterExclude.splice(index, 1)
                        this.setState({heroFilterExclude: newHeroFilterExclude})
                        this.updateHeroes(this.state.heroFilterInclude,newHeroFilterExclude, this.state.currentMode, this.state.heroProbs)
                    }
                    break;
                }
            }
        }
    }

    renderFilterBar () {
        const items = []
        for (const x of this.state.heroFilterInclude) {
            items.push(<Chip sx={{color: "black !important;"}}
                data-filtertype={"include"}
                data-filterheroid={x}
                label={"+" + HeroData.getHeroById(x)?.heroName || "" + x}
                onDelete={this.handleDeleteFilterChip}
                deleteIcon={<DeleteIcon/>}
                variant="outlined"
            />)
        }
        for (const x of this.state.heroFilterExclude) {
            items.push(<Chip sx={{color: "black !important;"}}
                data-filtertype={"exclude"}
                data-filterheroid={x}
                label={"-" + HeroData.getHeroById(x)?.heroName || "" + x}
                onDelete={this.handleDeleteFilterChip}
                deleteIcon={<DeleteIcon/>}
                variant="outlined"
            />)
        }

        return (<Box className={"filterBar"}>
            {items}
        </Box>)
    }

    setMode(e:any,value:string) {
        const currentMode=~~value

        this.setState({currentMode:currentMode,heroFilterInclude:[],heroFilterExclude:[]})
        this.updateHeroes([],[],currentMode,this.state.heroProbs)
    }

    render() {
        return (<Container sx={{
            bgcolor: 'background.default',
            display: "flex",
            flexDirection: "column",
            justifyContent: "flex-start",
            alignItems: "center",
        }}>
            <ToggleButtonGroup
                color="secondary"
                value={""+this.state.currentMode}
                exclusive
                onChange={this.setMode}
            >
                <ToggleButton value="0" color="primary">   </ToggleButton>
                <ToggleButton value="1" color="primary">   </ToggleButton>
                <ToggleButton value="8010" color="primary"> </ToggleButton>
                <ToggleButton value="8011" color="primary"> </ToggleButton>
                <ToggleButton value="8012" color="primary"> </ToggleButton>
                <ToggleButton value="8013" color="primary"> </ToggleButton>
                <ToggleButton value="8014" color="primary"> </ToggleButton>
                <ToggleButton value="8015" color="primary"> </ToggleButton>
            </ToggleButtonGroup>

            <Divider />
            <Divider><Chip label={this.state.requestsInProgress === 0?"  ":" ... !("+this.state.requestsInProgress+")"}/></Divider>
            {this.renderFilterBar()}
            <HeroCounterHeroGrid heroProbs={this.state.heroProbs}
                                 onFilterCounterHero={this.onHeroGridClick}
                                 addedHeroes={this.state.heroFilterInclude} removedHeroes={this.state.heroFilterExclude} key={10+this.state.currentMode+this.requestSeq}/>
            <Divider><Chip label={this.state.requestsInProgress === 0?" ":" ... ! ("+this.state.requestsInProgress+")"}/></Divider>
            <HeroCounterPackGrid heroLostPacks={this.state.heroIds}
                                 onFilterCounterHero={this.onHeroGridClick}
                                 heroCounterFilter={[]} key={20+this.state.currentMode+this.requestSeq} emptyMessage={" .     10   "+(this.state.currentMode===0?"":"")+".     "+(this.state.currentMode===0?"":"")+"  ,    ,    ."}/>
        </Container>)
    }

}